import React, { useEffect, useState } from 'react';
import { supabase } from '../utils/supabaseClient';

const AddProductInventoryForm = ({ onProductAdded, codigoInicial = '' }) => {
  const [product, setProduct] = useState({
    name: '',
    barcode: codigoInicial,
    quantity: 1
  });

  const [loadingCatalog, setLoadingCatalog] = useState(false);
  const [productoEnCatalogo, setProductoEnCatalogo] = useState(false);

  // Buscar el producto en catalogo_productos si hay un código escaneado
  useEffect(() => {
    const buscarEnCatalogo = async () => {
      if (!codigoInicial) return;

      setLoadingCatalog(true);
      const { data, error } = await supabase
        .from('catalogo_productos')
        .select('nombre')
        .eq('codigo_barras', codigoInicial)
        .single();

      if (error) {
        console.log('🔍 Producto no encontrado en catálogo. Se puede añadir manualmente.');
        setProductoEnCatalogo(false);
        setProduct(prev => ({
          ...prev,
          barcode: codigoInicial,
          name: ''
        }));
      } else {
        console.log('✅ Producto encontrado en catálogo:', data.nombre);
        setProductoEnCatalogo(true);
        setProduct(prev => ({
          ...prev,
          barcode: codigoInicial,
          name: data.nombre
        }));
      }

      setLoadingCatalog(false);
    };

    buscarEnCatalogo();
  }, [codigoInicial]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setProduct(prev => ({
      ...prev,
      [name]: name === 'quantity' ? parseInt(value) : value
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    const productoParaGuardar = {
      nombre: product.name,
      codigo_barras: product.barcode,
      cantidad: product.quantity
    };

    // 1. Buscar si ya existe en lista_compra
    const { data: existente, error: errorBuscar } = await supabase
      .from('lista_compra')
      .select('*')
      .eq('nombre', productoParaGuardar.nombre)
      .eq('codigo_barras', productoParaGuardar.codigo_barras)
      .single();

    if (errorBuscar && errorBuscar.code !== 'PGRST116') {
      // Código 116 = no se encontró nada, lo manejamos abajo
      console.error('❌ Error al buscar producto existente:', errorBuscar.message);
      alert('Error al comprobar duplicados');
      return;
    }

    if (existente) {
      // 2. Si existe → actualizar cantidad
      const nuevaCantidad = existente.cantidad + productoParaGuardar.cantidad;

      const { error: errorUpdate } = await supabase
        .from('lista_compra')
        .update({ cantidad: nuevaCantidad })
        .eq('id', existente.id);

      if (errorUpdate) {
        console.error('❌ Error al actualizar cantidad:', errorUpdate.message);
        alert('Error al actualizar el producto existente');
        return;
      }
      
    } else {
      // 3. Si no existe → insertar nuevo producto en lista_compra
      const { error: errorInsert } = await supabase
        .from('lista_compra')
        .insert([productoParaGuardar]);

      if (errorInsert) {
        console.error('❌ Error al insertar nuevo producto:', errorInsert.message);
        alert('Error al añadir el producto');
        return;
      }
    }

    // 4. Añadir a catalogo_productos si no está
    if (!productoEnCatalogo) {
      const { error: errorCatalogo } = await supabase.from('catalogo_productos').insert([
        {
          nombre: product.name,
          codigo_barras: product.barcode
        }
      ]);

      if (errorCatalogo) {
        console.error('⚠️ Error al guardar en catálogo:', errorCatalogo.message);
        alert('⚠️ El producto se guardó en la lista, pero no en el catálogo.');
      }
    }

    if (onProductAdded) onProductAdded();
    setProduct({ name: '', barcode: '', quantity: 1 });
  };


  return (
    <form onSubmit={handleSubmit} className="space-y-4">
      {loadingCatalog && <p className="text-sm text-gray-500">Buscando en el catálogo...</p>}

      <div>
        <label className="block text-sm font-medium text-gray-700">Nombre</label>
        <input
          type="text"
          name="name"
          value={product.name}
          onChange={handleChange}
          required
          className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3"
          placeholder="Nombre del producto"
        />
      </div>

      <div>
        <label className="block text-sm font-medium text-gray-700">Código de Barras</label>
        <input
          type="text"
          name="barcode"
          value={product.barcode}
          onChange={handleChange}
          required
          className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3"
          placeholder="Ej: 8431234567890"
        />
      </div>

      <div>
        <label className="block text-sm font-medium text-gray-700">Cantidad</label>
        <input
          type="number"
          name="quantity"
          min="1"
          value={product.quantity}
          onChange={handleChange}
          className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3"
        />
      </div>

      <button
        type="submit"
        className="w-full bg-green-600 text-white py-2 px-4 rounded-md hover:bg-green-700"
      >
        Agregar Producto
      </button>
    </form>
  );
};

export default AddProductInventoryForm;
