import React, { useState, useRef, useEffect } from 'react';
import Quagga from 'quagga';

  const ScanBarcodeButton = ({ onScan }) => {
    const [isScanning, setIsScanning] = useState(false);
    const [status, setStatus] = useState('');
    const videoRef = useRef(null);
    const streamRef = useRef(null);


    const beepSound = new Audio('scanner-beep.mp3'); // si está en /public

    const startScanner = () => {
    setIsScanning(true);
    setStatus('Iniciando escáner...');

    

    setTimeout(() => {
      Quagga.init({
        inputStream: {
          type: 'LiveStream',
          target: videoRef.current || document.querySelector('#video'),
          constraints: {
            width: 640,
            height: 480,
            facingMode: 'environment'
          }
          
        },
        decoder: {
          readers: ['ean_reader', 'code_128_reader', 'upc_reader']
        }
      }, (err) => {
        if (err) {
          console.error(err);
          setStatus('Error al iniciar el escáner');
          return;
        }
        Quagga.start();
        setStatus('Escaneando...');
      });

      Quagga.onDetected(handleDetected);
    }, 500); // Esperamos medio segundo para asegurar que el DOM esté listo
  };

  const handleDetected = (result) => {
    const code = result.codeResult.code;
    setStatus(`Código detectado: ${code}`);
    onScan(code);
    beepSound.play().catch(e => console.log('🔇 No se pudo reproducir el sonido:', e));
    stopScanner();
  };

  const stopScanner = () => {
    Quagga.offDetected(handleDetected);
    Quagga.stop();
    setIsScanning(false);
    setStatus('');
  };

  useEffect(() => {
    return () => {
      if (isScanning) stopScanner();
    };
  }, [isScanning]);

  return (
    <div className="relative">
      <button 
        onClick={isScanning ? stopScanner : startScanner}
        className={`py-2 px-4 rounded-lg transition-colors ${
          isScanning 
            ? 'bg-red-500 hover:bg-red-600 text-white'
            : 'bg-blue-500 hover:bg-blue-600 text-white'
        }`}
      >
        {isScanning ? 'Cancelar Escaneo' : 'Escanear Código'}
      </button>

      <div className="text-sm text-gray-600 mt-1">{status}</div>

      {isScanning && (
        <div className="fixed inset-0 bg-black z-50 flex flex-col items-center justify-center p-4">
          <div className="w-full max-w-md">
            <div className="flex justify-between items-center mb-2">
              <h2 className="text-lg font-bold text-white">Escaner de códigos</h2>
              <button 
                onClick={stopScanner}
                className="text-white text-xl p-2"
              >
                ✕
              </button>
            </div>

            <div
              ref={videoRef}
              className="relative w-full h-64 bg-black rounded-lg overflow-hidden"
              id="video"
            >
              {/* Rectángulo guía */}
              <div className="absolute top-0 left-0 right-0 bottom-0 flex items-center justify-center">
                <div className="border-2 border-red-400 w-64 h-40 relative">
                  <div className="absolute -top-1 -left-1 w-4 h-4 border-t-2 border-l-2 border-red-400"></div>
                  <div className="absolute -top-1 -right-1 w-4 h-4 border-t-2 border-r-2 border-red-400"></div>
                  <div className="absolute -bottom-1 -left-1 w-4 h-4 border-b-2 border-l-2 border-red-400"></div>
                  <div className="absolute -bottom-1 -right-1 w-4 h-4 border-b-2 border-r-2 border-red-400"></div>
                </div>
              </div>
            </div>
            <div className="mt-4 text-center text-white">
              <p>Enfoca el código de barras dentro del cuadro</p>
              <p className="text-yellow-300 font-medium mt-2">{status}</p>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default ScanBarcodeButton;