// src/hook/useCaducados.js
import { useState, useEffect } from 'react';
import { supabase } from '../utils/supabaseClient';

export const useCaducados = () => {
  const [caducados, setCaducados] = useState([]);
  const [loading, setLoading] = useState(true);

  const fetchCaducados = async () => {
    // Solo continuar si no hemos cargado antes
    if (caducados.length > 0) return;

    setLoading(true);
    const today = new Date().toISOString().split('T')[0];

    // Consulta SOLO los productos caducados
    const { data: dataCaducados, error: errorCaducados } = await supabase
      .from('caducados')
      .select('*');
   
    if (errorCaducados) {
      console.error('Error al cargar productos caducados:', errorCaducados);
      setCaducados([]);
    } else {
      setCaducados(dataCaducados);
    }

    setLoading(false);
  };

  useEffect(() => {
    fetchCaducados();
  }, []);

  return { caducados, loading, fetchCaducados, setCaducados };
};

