import React from 'react';
import { Inventario } from '../components/Inventario';

const ProductItem = ({ product, onEdit, onDelete, onAddToShoppingList }) => {
  const calculateDaysLeft = () => {
    if (!product.fecha_caducidad) return null;
    const expiry = new Date(product.fecha_caducidad);
    const today = new Date();
    const diffTime = expiry - today;
    return Math.ceil(diffTime / (1000 * 60 * 60 * 24));
  };

  const daysLeft = calculateDaysLeft();
  let statusClass = '';
  let statusText = '';

  if (daysLeft !== null) {
    if (daysLeft < 0) {
      statusClass = 'bg-red-200 text-red-800';
      statusText = 'Caducado';
    } else if (daysLeft <= 3) {
      statusClass = 'bg-yellow-200 text-yellow-800';
      statusText = `Caduca en ${daysLeft} días`;
    } else if (daysLeft <= 7) {
      statusClass = 'bg-blue-200 text-blue-800';
      statusText = `Caduca en ${daysLeft} días`;
    }
  }

  return (
    <div className="bg-white shadow rounded-lg p-4 mb-4">
      <div className="flex justify-between items-start">
        <div>
          <h3 className="font-medium text-lg">{product.nombre}</h3>
          <p className="text-gray-600">Cantidad: {product.cantidad}</p>
          <p className="text-gray-600">Categoría: {product.categorias?.nombre}</p>
          <p className="text-gray-600">Ubicación: {product.ubicaciones?.nombre}</p>
          {product.fecha_caducidad && (
            <p className="text-gray-600">
              Caduca: {new Date(product.fecha_caducidad).toLocaleDateString()}
            </p>
          )}
        </div>
        
        {statusText && (
          <span className={`px-2 py-1 text-xs font-semibold rounded-full ${statusClass}`}>
            {statusText}
          </span>
        )}
      </div>
      
      <div className="mt-4 flex space-x-2">
        <button
          onClick={() => onEdit(product)}
          className="text-sm bg-blue-500 text-white py-1 px-3 rounded hover:bg-blue-600"
        >
          Editar
        </button>
        <button
          onClick={() => onDelete(product.id)}
          className="text-sm bg-red-500 text-white py-1 px-3 rounded hover:bg-red-600"
        >
          Eliminar
        </button>
        {daysLeft !== null && daysLeft <= 0 && (
          <button
            onClick={() => onAddToShoppingList(product.id)}
            className="text-sm bg-green-500 text-white py-1 px-3 rounded hover:bg-green-600"
          >
            Añadir a compras
          </button>
        )}
      </div>
    </div>
  );
};

export default ProductItem;