import React, { useState, useEffect } from 'react';
import { supabase } from '../utils/supabaseClient';

export const SettingsModal = ({ isOpen, onClose }) => {
  const [newCategoria, setNewCategoria] = useState('');
  const [newUbicacion, setNewUbicacion] = useState('');
  const [categorias, setCategorias] = useState([]);
  const [ubicaciones, setUbicaciones] = useState([]);

  const fetchData = async () => {
    const { data: categoriasData } = await supabase.from('categorias').select('*');
    const { data: ubicacionesData } = await supabase.from('ubicaciones').select('*');
    setCategorias(categoriasData || []);
    setUbicaciones(ubicacionesData || []);
  };
  

  useEffect(() => {
    fetchData();
    if (isOpen) {
      fetchData();
    }
  }, [isOpen]);

  const handleAddCategoria = async () => {
    if (newCategoria.trim() === '') return;
    await supabase.from('categorias').insert([{ nombre: newCategoria.trim() }]);
    setNewCategoria('');
    fetchData();
  };

  const handleAddUbicacion = async () => {
    if (newUbicacion.trim() === '') return;
    await supabase.from('ubicaciones').insert([{ nombre: newUbicacion.trim() }]);
    setNewUbicacion('');
    fetchData();
  };

  const deleteCategoria = async (id) => {
    await supabase.from('categorias').delete().eq('id', id);
    fetchData();
  };

  const deleteUbicacion = async (id) => {
    await supabase.from('ubicaciones').delete().eq('id', id);
    fetchData();
  };



  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
      <div className="bg-white rounded-lg shadow-lg w-full max-w-md max-h-[90vh] flex flex-col">
        {/* <button
          onClick={onClose}
          className="absolute top-2 right-2 text-gray-600 hover:text-black"
        >
          ✕
        </button>*/}
        {/* Encabezado */}
        <div className="flex justify-between items-center p-4 border-b">
          <h2 className="text-lg font-semibold text-gray-800">Ajustes</h2>
          <button onClick={onClose} className="text-gray-500 hover:text-red-600 text-xl font-bold">
            &times;
          </button>
        </div>
        
        {/* Contenido desplazable */}
        <div className="overflow-y-auto p-4 space-y-4 flex-1">
          {/* Tu formulario de añadir categoría/ubicación y las listas existentes */}
          <div className="flex items-center justify-between">
            <div className="overflow-y-auto p-4 space-y-4 flex-1">
              {/* Añadir categoría */}
              <div className="mb-4">
                <label className="block mb-1 text-sm font-medium text-gray-700">Nueva Categoría</label>
                <input
                  type="text"
                  value={newCategoria}
                  onChange={(e) => setNewCategoria(e.target.value)}
                  className="w-full border border-gray-300 rounded text-gray-700 px-3 py-2"
                />
                <button
                  onClick={handleAddCategoria}
                  className="mt-2 bg-blue-600 text-white px-4 py-2 rounded hover:bg-blue-700"
                >
                  Añadir Categoría
                </button>
              </div>

              {/* Lista de categorías */}
              <h3 className="text-md font-semibold text-gray-700 mt-4 mb-1">Categorías existentes</h3>
              <ul className="space-y-1 mb-4">
                {categorias.map((cat) => (
                  <li key={cat.id} className="flex justify-between text-gray-700 items-center border-b py-1">
                    <span className='px-2'>{cat.nombre}</span>
                    <button
                      onClick={() => deleteCategoria(cat.id)}
                      className="text-red-500 hover:text-red-700 text-sm"
                    >
                      Eliminar
                    </button>
                  </li>
                ))}
              </ul>
            </div>
            <div className="overflow-y-auto p-4 space-y-4 flex-1">
              {/* Añadir ubicación */}
              <div className="mb-4">
                <label className="block mb-1 text-sm font-medium text-gray-700">Nueva Ubicación</label>
                <input
                  type="text"
                  value={newUbicacion}
                  onChange={(e) => setNewUbicacion(e.target.value)}
                  className="w-full border border-gray-300 text-gray-700 rounded px-3 py-2"
                />
                <button
                  onClick={handleAddUbicacion}
                  className="mt-2 bg-green-600 text-white px-4 py-2 rounded hover:bg-green-700"
                >
                  Añadir Ubicación
                </button>
              </div>

              {/* Lista de ubicaciones */}
              <h3 className="text-md font-semibold text-gray-700 mt-4 mb-1">Ubicaciones existentes</h3>
              <ul className="space-y-1">
                {ubicaciones.map((ubi) => (
                  <li key={ubi.id} className="flex justify-between text-gray-700 items-center border-b py-1">
                    <span className='px-2'>{ubi.nombre}</span>
                    <button
                      onClick={() => deleteUbicacion(ubi.id)}
                      className="text-red-500 hover:text-red-700 text-sm"
                    >
                      Eliminar
                    </button>
                  </li>
                ))}
              </ul>
            </div>
          </div>
          
        </div>

        
        {/* Pie opcional */}
        <div className="p-2 border-t text-center text-sm text-gray-400">
          © SmartFridge
        </div>
      </div>
    </div> 
        
  );
};

