import React, { useState, useEffect } from 'react';
import { supabase } from '../utils/supabaseClient';
import { useSupabaseData } from '../hook/useSupabaseData';

const AddProductForm = ({ onProductAdded, Inventario = null }) => {
  const [product, setProduct] = useState(Inventario || {
    id: '',
    name: '',
    barcode: '',
    quantity: 1,
    category: '',
    location: '',
    expiryDate: ''
  });

  const { data: categorias } = useSupabaseData('categorias');
  const { data: ubicaciones } = useSupabaseData('ubicaciones');

  useEffect(() => {
    const buscarProductoPorCodigo = async () => {
      if (!product.barcode) return;

      const { data, error } = await supabase
        .from('catalogo_productos')
        .select('*')
        .eq('codigo_barras', product.barcode)
        .limit(1);

      if (error) {
        console.error('Error buscando por código de barras:', error.message);
        return;
      }

      if (data.length > 0) {
        const existente = data[0];
        setProduct(prev => ({
          ...prev,
          name: existente.nombre,
          category: existente.categoria_id,
          location: existente.ubicacion_id,
          expiryDate: existente.fecha_caducidad
        }));
      }
    };

    buscarProductoPorCodigo();
  }, [product.barcode]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    if (name === 'quantity') {
      const qty = value === '' ? '' : parseInt(value, 10);
      setProduct(prev => ({ ...prev, quantity: qty }));
    } else {
      setProduct(prev => ({ ...prev, [name]: value }));
    }

    {/*setProduct(prev => ({
      ...prev,
      [name]: name === 'quantity' ? parseInt(value) : value
    }));*/}
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    const productoParaGuardar = {
      nombre: product.name,
      codigo_barras: product.barcode,
      fecha_caducidad: product.expiryDate,
      categoria_id: product.category,
      ubicacion_id: product.location,
      cantidad: product.quantity
    };

    // Insertar en catálogo si no existe
    const { data: productoCatalogo, error: errorCatalogo } = await supabase
      .from('catalogo_productos')
      .select('*')
      .eq('codigo_barras', productoParaGuardar.codigo_barras)
      .limit(1);

    if (errorCatalogo) {
      console.error('❌ Error consultando catálogo:', errorCatalogo.message);
    }

    if (!productoCatalogo || productoCatalogo.length === 0) {
      const { error: errorInsertarCatalogo } = await supabase
        .from('catalogo_productos')
        .insert([{
          nombre: productoParaGuardar.nombre,
          codigo_barras: productoParaGuardar.codigo_barras,
        }]);

      if (errorInsertarCatalogo) {
        console.error('❌ Error al insertar en catálogo:', errorInsertarCatalogo.message);
        alert('❌ No se pudo guardar el producto en el catálogo');
        return;
      }
    }

    if (Inventario && Inventario.id) {
      // Modo edición → actualizar cantidad directamente
      const { error: updateError } = await supabase
        .from('productos')
        .update({
          nombre: productoParaGuardar.nombre,
          codigo_barras: productoParaGuardar.codigo_barras,
          fecha_caducidad: productoParaGuardar.fecha_caducidad,
          categoria_id: productoParaGuardar.categoria_id,
          ubicacion_id: productoParaGuardar.ubicacion_id,
          cantidad: productoParaGuardar.cantidad
        })
        .eq('id', Inventario.id);

      if (updateError) {
        console.error('❌ Error al actualizar producto:', updateError.message);
        alert('❌ Error al actualizar el producto');
        return;
      }

      alert('✅ Producto actualizado correctamente');
    } else {
      // Modo nuevo → comprobar si ya existe
      const { data: existentes, error: fetchError } = await supabase
        .from('productos')
        .select('id, cantidad')
        .eq('codigo_barras', productoParaGuardar.codigo_barras)
        .eq('nombre', productoParaGuardar.nombre)

      if (fetchError) {
        console.error('❌ Error al buscar producto existente:', fetchError.message);
        alert('❌ Error buscando productos existentes');
        return;
      }

      if (existentes.length > 0) {
        const productoExistente = existentes[0];
        const nuevaCantidad = productoExistente.cantidad + productoParaGuardar.cantidad;
        console.log('🧩 Producto existente:', productoExistente);
        const { error: updateError } = await supabase
          .from('productos')
          .update({ cantidad: nuevaCantidad })
          .eq('id', productoExistente.id);

        if (updateError) {
          console.error('❌ Error al actualizar cantidad:', updateError.message);
          alert('❌ Error al actualizar el producto');
          return;
        }


        alert('✅ Producto actualizado correctamente');
      } else {
        const { error: insertError } = await supabase
          .from('productos')
          .insert([productoParaGuardar]);

        if (insertError) {
          console.error('❌ Error al insertar:', insertError.message);
          alert('❌ Error al guardar el producto');
          return;
        }

        alert('✅ Producto insertado correctamente');
      }
    }

    if (onProductAdded) onProductAdded();

    setProduct({
      name: '',
      barcode: '',
      quantity: 1,
      category: '',
      location: '',
      expiryDate: ''
    });
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-4">
      <div>
        <label className="block text-sm font-medium text-gray-700">Nombre</label>
        <input
          type="text"
          name="name"
          value={product.name ?? ''}
          onChange={handleChange}
          required
          className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3"
        />
      </div>

      <div>
        <label className="block text-sm font-medium text-gray-700">Código de Barras</label>
        <input
          type="text"
          name="barcode"
          value={product.barcode ?? ''}
          onChange={handleChange}
          className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3"
        />
      </div>

      <div>
        <label className="block text-sm font-medium text-gray-700">Cantidad</label>
        <input
          type="number"
          name="quantity"
          min="1"
          value={product.quantity ?? ''}
          onChange={handleChange}
          className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3"
        />
      </div>

      <div>
        <label className="block text-sm font-medium text-gray-700">Categoría</label>
        <select
          name="category"
          value={product.category ?? ''}
          onChange={handleChange}
          className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3"
        >
          <option value="">Seleccione una categoría</option>
          {categorias.map((cat) => (
            <option key={cat.id} value={cat.id}>{cat.nombre}</option>
          ))}
        </select>
      </div>

      <div>
        <label className="block text-sm font-medium text-gray-700">Ubicación</label>
        <select
          name="location"
          value={product.location ?? ''}
          onChange={handleChange}
          className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3"
        >
          <option value="">Seleccione una ubicación</option>
          {ubicaciones.map((ubi) => (
            <option key={ubi.id} value={ubi.id}>{ubi.nombre}</option>
          ))}
        </select>
      </div>

      <div>
        <label className="block text-sm font-medium text-gray-700">Fecha de Caducidad</label>
        <input
          type="date"
          name="expiryDate"
          value={product.expiryDate ?? ''}
          onChange={handleChange}
          className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3"
        />
      </div>

      <button
        type="submit"
        className="w-full bg-green-600 text-white py-2 px-4 rounded-md hover:bg-green-700"
      >
        {Inventario ? 'Actualizar Producto' : 'Agregar Producto'}
      </button>
    </form>
  );
};

export default AddProductForm;