import React from 'react';
import { useCaducados } from '../hook/useCaducados';
//import { supabase } from '../utils/supabaseClient';

const ExpiredProductItem = ({product, onDelete, onAddToShoppingList}) => {
  const { caducados, loading} = useCaducados();



  if (loading) return <p>Cargando productos caducados...</p>;
  if (caducados.length === 0) return <p>No hay productos caducados.</p>;
  //console.log('Renderizando producto:', product);
  return (
    
    <div className="bg-white shadow rounded-lg p-4 mb-4 border-l-4 border-red-500">
      <div className="flex justify-between items-start">
        <div>
          
          <h3 className="font-medium text-lg">{product.nombre}</h3>
          <p className="text-gray-600">Cantidad: {product.cantidad}</p>
          <p className="text-gray-600">Categoría: {product.categorias.nombre}</p>
          <p className="text-gray-600">Ubicación: {product.ubicaciones.nombre}</p>
          {product.fecha_caducidad && (
            <p className="text-gray-600">
              Caducó el:{' '}
              {new Date(product.fecha_caducidad).toLocaleDateString()}
            </p>
          )}
        </div>

        <span className="px-2 py-1 text-xs font-semibold rounded-full bg-red-100 text-red-800">
          Caducado
        </span>
      </div>
      
      <div className="mt-4 flex space-x-2">
        <button
          onClick={() => onAddToShoppingList(product)}
          className="text-sm bg-green-500 text-white py-1 px-3 rounded hover:bg-green-600"
        >
          Añadir a compras
        </button>
        <button
          onClick={() => onDelete(product)}
          className="text-sm bg-red-500 text-white py-1 px-3 rounded hover:bg-red-600"
        >
          Eliminar
        </button>
      </div>
    </div>
  )
};

export default ExpiredProductItem;

